<template>
  <div>
    <mu-appbar style="width: 100%;" :color="$store.state.color" class="header backgroundNaviBar">
      <div slot="left" color="#3051f1">
        <a @click="()=>{
           $router.go(-1)
          }">
          <mu-button icon size="20">
            <mu-icon color="white" value="keyboard_arrow_left"></mu-icon>
          </mu-button>
        </a>
      </div>
      <div style="width:100%;text-align:center;">
        <span style="font-size:18px;color:white;">处理结果</span>
      </div>
      <!-- <div slot="right" class="icon" size="20">
        <a href="#">
          <mu-button icon slot="left" class="icon" size="20">
            <mu-icon value="list" size="24" color="white"></mu-icon>
          </mu-button>
        </a>
      </div>-->
    </mu-appbar>
    <div class="backHandle">
      <div>
        <div class="backWhite">
          <div class="marin-bottom">
            <span>处理结果及描述</span>
          </div>
          <textarea name id="textarea" placeholder="请输入处理结果及描述" v-model="processingMark"></textarea>
        </div>
        <div class="backWhite">
          <div class="marin-bottom">
            <span>现场照片</span>
          </div>
          <mu-container>
            <mu-expansion-panel
              :expand="panel === 'panel1'"
              @change="toggle('panel1')"
              class="paddingHandle"
              v-for="(item,index) in itemListPic"
              :key="index"
            >
              <div slot="header">图片{{index+1}}</div>
              <div class="imageSize" v-for="(itemChild ,index1) in item.livePhoto" :key="index1">
                <img :src="url+itemChild" alt />
              </div>
            </mu-expansion-panel>
          </mu-container>
          <el-upload
            ref="upload"
            action="http://47.112.16.168:8800/event/upload"
            multiple
            name="files"
            list-type="picture-card"
            :on-change="handleRemove"
            :http-request="uploadFile"
            :auto-upload="false"
            :limit="4"
            :on-exceed="limitStatus"
          >
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible" size="tiny">
            <img width="100%" :src="dialogImageUrl" alt />
          </el-dialog>
        </div>

        <div class="handlePerson backWhite">
          <div class="person">
            <span>处理人</span>
          </div>
          <input id="input" disabled v-model="processingPerson" type="text" />
        </div>
      </div>
      <div>
        <div class="submit backgroundNaviBar" @click="submit()">
          <span>提交</span>
        </div>
      </div>
    </div>
    <van-loading type="spinner" color="#1989fa" v-show="showLoading" />
  </div>
</template>
<script>
import request from "../../../static/js/request.js";
import Warning from "../../../static/js/warning.js";
import { Toast } from "vant";
import Vue from "vue";
import { Loading } from "vant";
Vue.use(Loading);
export default {
  name: "handle",
  data() {
    return {
      panel: "",
      itemListPic: [],
      token: "",
      id: "",
      url: "http://47.112.16.168:",
      dialogImageUrl: "",
      dialogVisible: false,
      formDate: {},
      processingMark: "",
      processingPerson: "",
      showLoading: false
    };
  },
  methods: {
    limitStatus() {},
    uploadFile(res) {
      this.formDate.append("files", res.file);
    },
    handleRemove(file, fileList) {
      if (fileList.length > 0) {
        this.statusPictrue = true;
      } else {
        this.statusPictrue = false;
      }

      console.log("filelist", fileList);
    },
    toggle(panel) {
      this.panel = panel === this.panel ? "" : panel;
    },
    async initData() {
      var url = "/warning/list";
      var waterStation = "";
      var starttime = "";
      var lasttime = "";
      var pageSize = 5;
      var pageNum = 1;
      var res = await request.request(
        url,
        "POST",
        {
          waterStation,
          starttime,
          lasttime,
          pageSize,
          pageNum
        },
        this.token
      );
      console.log(res.data.data.data);
      this.itemListPic.push(Warning.filterData(res.data.data.data, this.id));
      $("#input").val(this.itemListPic[0].processingPerson);
      $("#textarea").val(this.itemListPic[0].processingMark);

      // this.listWarningContent = res.data.data.data;
    },
    async submit() {
      var _this = this;
      var url = "";
      this.showLoading = true;
      this.formDate = new FormData();
      this.$refs.upload.submit();
      this.formDate.append("processingPerson", this.processingPerson);
      this.formDate.append("processingMark", this.processingMark);
      // console.log(typeof(this.id));
      if (typeof this.id === "object") {
        this.formDate.append("warningIdList", this.id);
        url = "/warning/update/batch";
      } else {
        this.formDate.append("warningId", this.id);
        url = "/warning/update";
      }
      let config = {
        headers: {
          "Content-Type": "multipart/form-data",
          token: this.token
        }
      };
      this.$http
        .post(url, this.formDate, config)
        .then(res => {
          // console.log("res", res);
          // console.log("message", res.data.message);
          _this.showLoading = false;
          if (res.data.info === "处理成功") {
            Toast.success(res.data.info + ",正在更新数据库！");
            if (
              _this.$route.query.index !== undefined &&
              _this.$route.query.index !== null
            ) {
              _this.$store.state.websocketWarningData.splice(
                _this.$route.query.index,
                1
              );

              // console.log(_this.$store.state.websocketWarningData);
            }
            _this.$router.go(-1);
          }
        })
        .catch(err => {
          // console.log(res);
          Toast.error(err.data.info + ",请重试！");
        });
    }
  },
  mounted() {
    this.token = sessionStorage.getItem("token");
    if (this.$route.query.status === true) {
      var array = this.$store.state.warningIdList;
      // /warning/update/batch
      var arr = [];
      for (let index = 0; index < array.length; index++) {
        var id = array[index].warningId;
        arr.push(id);
      }
      this.id = arr;
    } else {
      this.id = this.$route.query.id;
    }
    this.processingPerson = sessionStorage.getItem("user");
    // this.initData();
  }
};
</script>
<style scoped>
#textarea {
  width: 100%;
  height: 100px;
  border-radius: 10px;
  /* border: #ccc 1px solid; */
  border: rgb(231, 231, 231) 1px solid;

  outline: none;
  padding: 5px;
  background: white;
}
.backHandle {
  color: rgb(122, 122, 122);
  padding: 10px;
  margin-top: 60px;
}
.backWhite {
  background: white;
  padding: 10px;
  border-radius: 10px;
  margin-bottom: 5px;
}
.handlePerson {
  display: flex;
  flex-direction: row;
  margin-top: 10px;
  justify-content: space-between;
}
#input {
  border-radius: 10px;
  border: rgb(231, 231, 231) 1px solid;
  /* border: none; */
  background-color: white;
  display: flex;
  flex: 1;
  margin-left: 10px;
  outline: none;
  padding: 0 5px;
  height: 40px;
}
.imageSize img {
  width: 100%;
  height: 200px;
}
.submit {
  width: 100%;
  text-align: center;
  height: 40px;
  /* background: #4ed995; */
  border-radius: 10px;
  color: white;
  font-size: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
}
.header {
  position: fixed;
  top: 0;
}
.paddingHandle {
  box-shadow: none;
}
.person {
  display: flex;
  justify-content: center;
  align-items: center;
}
.marin-bottom{
  margin-bottom: 10px;
}
</style>



